/*
 * Font Declarations
 */
@font-face {
  font-family: 'Glacial Indifference';
  src: local("Glacial Indifference"), url("../assets/GlacialIndifference-Regular.otf") format("opentype"); }

@font-face {
  font-family: 'Cheddar Gothic Sans';
