/***************************************
 *       Global GAD Styling
 ***************************************/
/*
 * Element Overrides
 */
/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-family: "PT Sans", sans-serif;
  background-color: #95b7e5;
  overflow-x: hidden;
  color: #1c3749; }

img {
  max-width: 100%;
  max-height: 100%; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Cheddar Gothic Sans";
  color: #1c3749;
  margin: 8px 0;
  font-weight: 400;
  line-height: 1; }
  h1 span,
  h2 span,
  h3 span,
  h4 span,
  h5 span,
  h6 span {
    display: block; }
  h1.subheading,
  h2.subheading,
  h3.subheading,
  h4.subheading,
  h5.subheading,
  h6.subheading {
    font-family: "Glacial Indifference";
    color: #1c4c6c;
    font-weight: bold;
    text-transform: uppercase; }

h2 {
  letter-spacing: 0px; }

.text-color-light {
  color: #1c4c6c; }

.text-color-dark {
  color: #1c3749; }

/*
 * UI Element Styling
 */
.page {
  position: relative;
  height: calc(100vh - 64px);
  padding: 16px; }
  .page .page-cta {
    font-family: "Cheddar Gothic Sans";
    position: absolute;
    bottom: 20px;
    right: 40px;
    font-size: 32px;
    text-decoration: none;
    color: #1c3749; }

.speech-bubble {
  position: relative;
  display: inline-block;
  background-color: #fff;
  border-radius: 12px;
  padding: 32px; }
  .speech-bubble:after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border-top: 24px solid #fff;
    border-right: 36px solid transparent; }
  .speech-bubble.reversed:after {
    border-right: none;
    border-left: 36px solid transparent; }
  .speech-bubble.decorative {
    height: 120px;
    width: 140px; }

.stylized-button {
  display: inline-block;
  font-family: "Glacial Indifference";
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  color: #1c3749;
  letter-spacing: 2px;
  border-radius: 0;
  background-color: #fff;
  border: 1px solid #d9d9d9;
  padding: 16px;
  cursor: pointer; }

.stylized-button-small {
  padding: 8px; }

/*
 * UI Element Style accents
 */
/* Bezel borders */
.bezel-xs {
  background-color: #fff;
  border-top: 2px solid #eee;
  border-left: 2px solid #eee;
  border-bottom: 2px solid #e0e0e0;
  border-right: 2px solid #e0e0e0; }

.bezel-s {
  background-color: #fff;
  border-top: 4px solid #eee;
  border-left: 4px solid #eee;
  border-bottom: 4px solid #e0e0e0;
  border-right: 4px solid #e0e0e0; }

.bezel-m {
  background-color: #fff;
  border-top: 8px solid #eee;
  border-left: 8px solid #eee;
  border-bottom: 8px solid #e0e0e0;
  border-right: 8px solid #e0e0e0; }

.bezel-l {
  background-color: #fff;
  border-top: 16px solid #eee;
  border-left: 16px solid #eee;
  border-bottom: 16px solid #e0e0e0;
  border-right: 16px solid #e0e0e0; }

/* Circles */
.circle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border-radius: 50%;
  background-color: #fff;
  line-height: 1;
  font-family: "Cheddar Gothic Sans";
  color: #1c4c6c; }
  .circle p {
    margin: 0; }
  .circle:first-line {
    color: #1c3749; }
  .circle p:nth-of-type(2) {
    margin-top: 8px;
    font-size: 16px; }

.circle-l {
  width: 70vw;
  height: 70vw;
  padding: 10vw;
  font-size: 40px; }

.circle-m {
  width: 30vw;
  height: 30vw;
  font-size: 40px; }

.circle-s {
  width: 15vw;
  height: 15vw;
  font-size: 30px; }

/* drop-shadows */
.drop-shadow {
  filter: drop-shadow(3px 3px 3px #657cc9); }

/* Backgrounds */
.paper-background {
  position: relative;
  background: #fff;
  border-radius: 0 12px 0 12px;
  padding: 16px 8px; }
  .paper-background:after {
    content: '';
    position: absolute;
    right: 0;
    top: 100%;
    border-top: 16px solid #dedfe0;
    border-left: 20px solid #dedfe0;
    border-right: 20px solid transparent;
    border-bottom: 16px solid transparent; }
  .paper-background:before {
    content: '';
    position: absolute;
    right: 5%;
    top: calc(100% + 33px);
    width: 80%;
    box-shadow: 0px 2px 10px 5px #657cc9; }

/*
 * Animations
 */
@keyframes fade-in {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fade-in-delayed {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fade-in-and-grow {
  from {
    opacity: 0;
    transform: scale(0); }
  to {
    opacity: 1;
    transform: scale(1); } }

@keyframes fade-in-and-twirl {
  0% {
    opacity: 0;
    filter: none;
    transform: rotate(-760deg); }
  50% {
    opacity: 1;
    filter: none;
    transform: rotate(0deg); }
  100% {
    opacity: 1;
    filter: none;
    filter: drop-shadow(0px 0px 3px #657cc9); } }

@keyframes fly-in-right-with-shadow {
  0% {
    transform: translateX(100vw);
    filter: none; }
  60% {
    transform: translateX(0);
    filter: none; }
  80% {
    transform: translateX(0);
    filter: drop-shadow(3px 3px 3px #657cc9); }
  100% {
    transform: translateX(0);
    filter: drop-shadow(3px 3px 3px #657cc9); } }

@keyframes fly-in-bottom {
  0% {
    transform: translateY(100vh); }
  50% {
    transform: translateY(0); }
  100% {
    transform: translateY(0); } }

@keyframes fly-in-bottom-with-shadow {
  0% {
    transform: translateY(100vh);
    filter: none; }
  50% {
    transform: translateY(0);
    filter: none; }
  100% {
    transform: translateY(0);
    filter: drop-shadow(3px 3px 3px #657cc9); } }
