    height: 100%; }

.main-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9999;
  transition: transform 0.3s ease;
  background-color: #fff; }
  .main-menu.hidden {
