.page.welcome {
  overflow: hidden; }

.explore-button {
  margin-top: 35%; }

.top-container {
  z-index: 10;
  height: 30%; }

.title-image {
  margin-top: 32px; }

.bottom-container {
  position: relative;
  height: 65%;
  z-index: 1; }

.welcome-animation {
  position: absolute;
  width: 60%;
  height: 100%;
  right: 0;
  bottom: 0; }
  .welcome-animation .animation-container {
    position: absolute;
    height: 100%;
    width: 100%; }
  .welcome-animation .speech-bubble-container {
    position: absolute;
    animation-name: fly-in-bottom-with-shadow;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    will-change: transform;
    transform: translateY(100vh); }
    .welcome-animation .speech-bubble-container:nth-child(1) {
      top: -5%;
      right: 5%;
      animation-delay: 0s; }
      .welcome-animation .speech-bubble-container:nth-child(1) .speech-bubble {
        transform: scale(0.3); }
    .welcome-animation .speech-bubble-container:nth-child(2) {
      top: 5%;
      right: 30%;
      animation-delay: 0.1s; }
      .welcome-animation .speech-bubble-container:nth-child(2) .speech-bubble {
        transform: scale(0.2); }
    .welcome-animation .speech-bubble-container:nth-child(3) {
      top: 20%;
      right: 40%;
      animation-delay: 0.2s; }
      .welcome-animation .speech-bubble-container:nth-child(3) .speech-bubble {
        transform: scale(0.75); }
    .welcome-animation .speech-bubble-container:nth-child(4) {
      top: 20%;
      right: -15%;
      animation-delay: 0.3s; }
      .welcome-animation .speech-bubble-container:nth-child(4) .speech-bubble {
        transform: scale(0.5); }
    .welcome-animation .speech-bubble-container:nth-child(5) {
      top: 50%;
      right: -16%;
      animation-delay: 0.4s; }
      .welcome-animation .speech-bubble-container:nth-child(5) .speech-bubble {
        transform: scale(0.8); }
    .welcome-animation .speech-bubble-container:nth-child(6) {
      top: 35%;
      right: 10%;
      animation-delay: 0.5s; }
      .welcome-animation .speech-bubble-container:nth-child(6) .speech-bubble {
        transform: scale(0.6); }
    .welcome-animation .speech-bubble-container:nth-child(7) {
      top: 65%;
      right: 40%;
      animation-delay: 0.6s; }
  .welcome-animation .shadow {
    position: absolute;
    height: 0px;
    z-index: -100;
    opacity: 0;
    box-shadow: 0px 0px 10px 3px #657cc9;
    animation-name: fade-in-delayed;
    animation-duration: 1.5s;
    animation-fill-mode: forwards;
    animation-delay: 0.5s;
    will-change: transform; }
    .welcome-animation .shadow:nth-child(1) {
      width: 50%;
      right: 50%;
      bottom: 0; }
    .welcome-animation .shadow:nth-child(2) {
      width: 30%;
      right: 5%;
      bottom: 4%; }
    .welcome-animation .shadow:nth-child(3) {
      width: 30%;
      right: 40%;
      bottom: 3%; }
  .welcome-animation .star {
    position: absolute;
    display: inline-block;
    width: 0;
    height: 0;
    z-index: 10;
    opacity: 0;
    animation-name: fade-in-and-twirl;
    animation-fill-mode: forwards;
    animation-duration: 1.5s;
    margin-left: 0.9em;
    margin-right: 0.9em;
    margin-bottom: 1.2em;
    border-right: 0.3em solid transparent;
    border-bottom: 0.7em solid #fff;
    border-left: 0.3em solid transparent;
    /* Controlls the size of the stars. */
    font-size: 12px; }
    .welcome-animation .star:before, .welcome-animation .star:after {
      content: '';
      display: block;
      width: 0;
      height: 0;
      position: absolute;
      top: 0.6em;
      left: -1em;
      border-right: 1em solid transparent;
      border-bottom: 0.7em solid #fff;
      border-left: 1em solid transparent;
      transform: rotate(-35deg); }
    .welcome-animation .star:after {
      transform: rotate(35deg); }
    .welcome-animation .star:nth-child(1) {
      top: -10%;
      right: 30%;
      animation-delay: calc(0.5s + 1.5s); }
    .welcome-animation .star:nth-child(2) {
      top: -0%;
      right: 5%;
      animation-delay: calc(0.6s + 1.5s); }
    .welcome-animation .star:nth-child(3) {
      top: 20%;
      right: 38%;
      animation-delay: calc(0.7s + 1.5s); }
    .welcome-animation .star:nth-child(4) {
      top: 45%;
      right: 10%;
      animation-delay: calc(0.8s + 1.5s); }
    .welcome-animation .star:nth-child(5) {
      top: 55%;
      right: 75%;
      animation-delay: calc(0.9s + 1.5s); }
